import {forms, modalDirector, modalBig} from './constants.js'
import submitForm from '../modules/submitForms.js'

function POST() {
    forms.forEach(form => {
        if (form.classList.contains('director')) {
            submitForm(form, modalDirector, 'mailer2/smart.php')
        } else {
            submitForm(form, modalBig, 'mailer/smart.php')
        }
    })
}

export default POST
