import {overlayModal, modalMini} from '../services/constants.js'

function submitForm(form, modal, link) {
    form.addEventListener('submit', function (e) {
        e.preventDefault()

        fetch(link, {
            method: 'POST',
            body: new FormData(this),
        })
            .then(function (response) {
                if (response.ok) {
                    form.querySelectorAll('input').forEach(input => {
                        input.value = ''
                    })
                    modal.classList.remove('modal__active')
                    overlayModal.classList.add('overlay-modal__active')
                    modalMini.classList.add('modal__active')

                    setTimeout(function () {
                        overlayModal.classList.remove('overlay-modal__active')
                        modalMini.classList.remove('modal__active')
                    }, 5000)

                    form.reset()
                }
            })
            .catch(error => {
                console.log('Error:', error)
            })

        return false
    })
}

export default submitForm
